/* cta: container ft: feature*/
/* General Styles -- later arrange according Alphabetically */
a{
  background-color: transparent;
  text-decoration: none;
}
img{
  /* display: inline-block; */
  max-width: 100%;
  vertical-align: middle;
}
h1{
  font-family: "Hanken book";
  font-size: 4rem;
  text-align: center;
  margin-bottom: 0.8rem;
  font-weight: 400;
  line-height: 1.1em;
  text-transform: uppercase;
}
h2{
  font-family: "Hanken book";
  font-size: 3.6rem;
  text-transform: uppercase;
  line-height: 1.1em;
  font-weight: 400;
  color: #2d2c4c;
}
h3{
  font-family: "Hanken book";
  font-size: 2.6rem;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 1.6rem;
  line-height: 1.3em;
}
p{
  color: #2d2c4c;
  font-size: 1.6rem;
  line-height: 1.5;
  margin-bottom: 3.2rem;
}
button{
  font-family: "Hanken book";
  display: inline-block;
  border-radius: 4px;
  background-color: #3898EC;
  color: white;
  border: 0;
  cursor: pointer;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}
ul{
  list-style: none;
  text-align: center;
}

/* Grouped Styles  -- later arrange according Alphabetically*/
.section{
  padding-top: 4.8rem;
  padding-bottom: 3.2rem;
  text-align: center;
  line-height: 1.5em;
}
.label{
  display: inline-block;
  font-size: 1.4rem;
    line-height: 1.7em;
    background-color: #2d2c4c;
    color: #fff;
    margin-bottom: 3.2rem;
    padding: 0 0.8rem;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
  .title{
    font-family: "Hanken book";
    font-size: 1.6rem;
    color: #20a5e6;
    line-height: 1.2em;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    /* padding-bottom: 0.5rem; */
  }
  .container{
    padding: 0 1.6rem;
  }
  .section-header{
    margin-bottom: 3.2rem;
  }
  .subtitle{
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 1.5em;
    text-align: center;
    color: #2d2c4c;
    margin-bottom: 4.8rem;
  }
  .item-img{
    display: inline-block;
    height: 66px;
    margin-bottom: 3.2rem;
  }

  /* Hero Section*/
  .hero{
    background-color: #fcd750;
    text-align: center;
    padding-top: 8rem;
    padding-bottom: 3.2rem;
    color: #2d2c4c;
  }
  #hero-img{
    display: inline-block;
    width: 92%;
    margin-bottom: 3.2rem;
  }
  .hero-list{
    /* padding: 0 2rem; */
  }
  .item{
    margin-bottom: 2.4rem;
  }
  .item-btn{
    display: inline-block;
    font-family: "Hanken book";
    border-radius: 3px;
    font-size: 1rem;
    background-color: #20a5e6;
    padding: 1.4rem 1.3rem 1.1rem;
    letter-spacing: 0.1em;
  }

  /* Process Section */
  .process{
    background-color: white;
    color: #2d2c4c;
  }

  /* Features Section */
  .features{
    background-color: #bce3f5;
    color: #2d2c4c;
  }
  .ft-item{
    padding: 0 1.6rem;
    margin-bottom: 3.2rem;
  }
  .ft-img{
    display: inline-block; /* Fix Later */
    width: auto;
    margin-bottom: 3.2rem;
  }

  /* Services Section */
  .services{
    background-color: #f2f2f2;
  }

  /* About Section */
  .about{
    background-color: #fcd750;
    color: #2d2c4c;
    padding: 3.2rem 0;
    text-align: center;
  }
  .about .container{
    margin: 0 3.2rem;
  }
  .about button{
    font-size: 1.4rem;
    background-color: #2d2c4c;
    padding: 2rem 2.4rem 1.6rem;
  }

  /* How Section */
  .how{
    background-color: #f2f2f2;
  }
  .how .subtitle{
    margin-bottom: 0;
  }
  .how button{
    font-size: 1.4rem;
    background-color: #20a5e6;
    padding: 2rem 2.4rem 1.6rem;
  }

  /* Footer Section */
  .footer{
    padding: 3.2rem 0;
    background-color: #2d2c4c;
  }
  .footer-content{
    line-height: 1.4em;
    margin-bottom: 1.6rem;
  }
  .footer-text{
    font-size: 1.6rem;
    margin-bottom: 1.6rem;
    color: #fff;
  }
  .footer-content ,.footer-content a{
    color: #fcd750;
    font-size: 1.6rem;
  }
  .footer-content--title{
    margin-bottom: 1.6rem;
  }
  .icon{
    display: inline-block;
    width: 3.2rem; /*12%*/
    margin: 0 0.8rem;
    margin-bottom: 3.2rem;
  }
  /* Sub Styles */
  .cta-icon{
    display: inline-block;
    width: 23%;
    margin-bottom: 3.2rem;
  }
  .footer-text{
    margin-bottom: 2.4rem;
  }
  .footer-content h3{
    color: #20a5e6;
    font-size: 1.6rem;
  }
